import React from "react";
import Layout from "./Layout";
import { graphql, Link } from "gatsby";
import { SEO } from "theme-seo";
import Img from "gatsby-image";

const Plans = ({ data, pageContext }) => {
  const markdownNodes = data.allMarkdownRemark.nodes
    .sort((a, b) => a.frontmatter.id - b.frontmatter.id);
  const templateIcon = data.allAsnafImages.nodes.filter(image => image.name === "default")[0]
  return (
    <Layout faqs={pageContext.faqs}>
      <SEO title={pageContext.title} imageUrl={data.allFile.nodes.length !== 0 && data.allFile.nodes[0].publicURL} faqs={pageContext.faqs} />
      {data.allFile.nodes.length !== 0 && <Img alt="features" fluid={data.allFile.nodes[0].childImageSharp.fluid}/>}
      <div
        className="flex flex-wrap my-24 justify-center lg:flex-row w-10/12 lg:w-5/6 mx-auto shadow-xl">
        {markdownNodes ? (markdownNodes.map(node => {
        const icon = data.allAsnafImages.nodes.filter(image => node.frontmatter.icon === image.name).length === 1  ?
            data.allAsnafImages.nodes.filter(image => node.frontmatter.icon === image.name)[0] : templateIcon
         return <Link to={`/plans/${node.frontmatter.title.replace(/ /g, "-")}?senfId=${node.frontmatter.id}`} className="m-1"
          data-aos="fade-up" key={node.id}>
            <div key={node.frontmatter.videoName}>
              <div>
                <div>
                  <div>
                    <div className="shadow-md m-4 p-2 hover:shadow-xl" key={node.fields.slug}>
                      <img className="m-auto w-64 h-64 "
                          src={icon.publicURL}
                          alt={node.frontmatter.title}
                          width="100" height="100"
                      />
                    </div>
                    <h4 className="text-gray-700" style={{fontSize:14, textAlign:"center"}}>{node.frontmatter.title}</h4>
                  </div>
                </div>
              </div>
            </div>
          </Link>
        })) : (
          <div className="m-auto text-purple-600 my-10 font-bold text-4xl">There is no videos to show</div>)}
      </div>
    </Layout>
  );
};

export default Plans;

export const query = graphql`
query allAsnaf {
  allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/asnaf/"  }})
   {
    nodes {
      html
      fields {
        slug
      }
      frontmatter {
        title
        description
        icon
        id
      }
      parent {
          ... on File {
            birthTime(formatString: "MM/DD/YYYY")
          }
        }
    }
  }

  allAsnafImages:allFile(filter: {absolutePath: {regex: "/asnaf/"  }, extension:{eq: "svg"} }) {
    nodes {
      name
      publicURL
      childImageSharp {
        fixed(width:1200){
         src
         }
       }
    }
  }

  allFile(filter: {absolutePath: {regex: "/asnaf/"  }, extension:{eq: "jpg"}, name:{eq:"asnaf"}}) {
    nodes {
      name
      publicURL
      childImageSharp {
        fluid (maxWidth: 2500 ) {
          ...GatsbyImageSharpFluid
        }
       }
    }
  }
}`;